.app {
    position: relative;
    width: 100%;
    height: 100vh;
    margin: 0 auto;
    background: linear-gradient(180deg, #E6D4DE 0%, #9890C7 100%);

}

.preloaderApp {
    margin: 0;
    position: fixed;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%)
}